import { Layout, Tag, BackTop, Typography } from 'antd';
import instructions from '../data/instructions';
import opcodes from '../data/opcodes';
import { Card} from 'antd';
const { Header, Content, Footer } = Layout;
const { Text } = Typography;

function App() {
    const gridStyle = {
        width: '100%',
        textAlign: 'left',
      };

      const gridStyle2 = {
        width: '25%',
        textAlign: 'center',
      };

    return (
        <div>
            <BackTop />
            <br/>
            <h2>Instructions</h2>
            <Card>
            { instructions.map(inst => {
                if (inst) return (
                    <a href={`#${inst.mnemonic}`}>
                        <Card.Grid style={gridStyle2}>
                            <h3>{inst.mnemonic}</h3>
                        </Card.Grid>
                    </a>
                )
            }) }
            </Card>
            <br/><br/><br/><br/>
            <h2>Descriptions</h2>
            { instructions.map(inst => {
                if (inst) return (
                    <Card id={inst.mnemonic} title={inst.mnemonic} style={{marginBottom: '20px'}} extra={<Tag color="red">{inst.group}</Tag>}>
                        <Card.Grid style={gridStyle}>
                            {inst.description}
                            <br/>
                            {inst.useage && inst.useage.map(u => <div><Text code>{u}</Text></div>)}
                            <br/>
                            <h4>Opcodes</h4>
                            {opcodes.map(op => {
                                if (op.mnemonic.includes(inst.mnemonic)) {
                                    return <Tag>{`0x${op.opcode}`}</Tag>
                                }
                            })}
                            <br/>
                            {inst.flags && <br/> }
                            {inst.flags.length > 0 ? <h4>Flags</h4> : null}
                            {inst.flags && inst.flags.map(flag => <Tag>{flag}</Tag>)}
                        </Card.Grid>
                    </Card>
            )}) }
        </div>
    );
}

export default App;
